import React from "react"
import Constellation from "../images/constellation.jpg"
import { Input } from "./input";

interface IWordLockViewProps {
    onBack: () => void;
    onDone: () => void;
}

export function WordLockView(props: IWordLockViewProps): JSX.Element {
    const validateInput = (key: string) => {
        if (key.length != 1 || !key.match(/[a-z]/i)) {
            return undefined;
        }

        return key.toUpperCase();
    }

    return (
        <div className="lock">
            <div className="txt">
                <h1>Telescope calibration</h1>
                <p>You need the telescope to figure out the best time to launch. But first, you need to calibrate the telescope.</p>
                <p>To calibrate the telescope, you need to point it to a known constellation and input the constellation's name.</p>
                <p>You see the constellation below in the night sky...</p>
            </div>
            <div style={{ marginTop: "20px", marginBottom: "20px"}}>
                <img src={Constellation} style={{ margin: "auto", display: "block" }}></img>
            </div>
            <Input expected="SAGITTARIUS" onDone={props.onDone} validateInput={validateInput}></Input>
            <div>
                <p>... or you could always <a href="#" onClick={props.onBack}>go back</a> and try again later.</p>
            </div>
        </div>);
}


